<template>
   <div>
        <div id="zzmb" v-show="zzshow" class="zzmb" style="display:none;position:fixed;"></div>
        <div v-show="page== 'main'"  class="address">
            <div class="addr_area" @click="choseAddress">
                <div class="addrtop">&nbsp;</div>
                <div style="text-align:center;background-color: #f7f7f1;">
    					<a href="#" class="add_address" v-show="!address.receiveName"></a>
    			</div>
                <div class="custom-menu-link" v-show="address.receiveName">
    					<a class="sustoma"  href="#">
    						<span>{{address.receiveName}}</span>
    						<span style="margin-left:15px;">{{address.tel}}</span>
    						<div class="addr_location">{{address.province}}{{address.city}}{{address.county}}{{address.xiaoquName}}{{address.detailAddress}}</div>
    					</a>
    			</div>
                <div class="addrbottom">&nbsp;</div>
            </div>
            <div class="contain">
                <div class="item">
    				<span class="span1"></span>
    				<span class="span2"></span>
    			</div>
                <div class="time" style="line-height: 30px;">
    				<span class="span1"></span>
    				<img @click="addhours" src="../assets/img/btn_plus_03.png" class="icon1" />
    				<span style="float: right; margin-right: 6px; font-size: 14px;">{{hours}}</span>
    				<img  @click="subhours" src="../assets/img/btn_subtract_03.png" class="icon2"/>
    			</div>
                <div class="total">
    				<span class="span1"></span>
    				<span class="span4"><span style="padding-left: 4px;">{{amount}}</span></span>
    			</div>
            </div>
            <div class="discount" @click="showCoupons">
                <span class="span1"></span>
    			<img src="../assets/img/icon_youhuiquan_03.png" class="icon3" />
    			<span class="span1_1">{{couponNum}}</span>
    			<img src="../assets/img/btn_arrow_05_05.png" class="icon4" />
    			<span class="span1_2" v-show="coupon.amount">-{{coupon.amount}}</span>
            </div>
            <div class="custom_menu">
                <div class="order_time"  id="timetaker">
    				<span class="span1"></span>
    				<img src="../assets/img/btn_arrow_05_05.png" class="icon4" />
    				<input class="date_pppp" id="datetimepicker2" v-model="requireDate">
                    <span class="tip" v-show="!requireDate"></span>
                    <span class="tip">{{requireDate}}</span>
    			</div>
                <div class="custom_notice" @click="showMemo">
    				<span class="custom_notice_span">{{memo}}</span>
    			</div>
                <div class="total_pay">
    			    <div class="money"><span>{{realAmount }}</span></div>
    			<span class="paybtn" @click="submit"></span>
    	    	</div>
            </div>
        </div>
        <!--  -->
        <div v-show="page=='list'" class="addrlist">
            <div v-show="lian=='chu'">
                <div   class="plr15s arrow-margins menu-links mt1s fs14s address-wraps lite-dividers" v-for="item in addresses" @click="checks(item)">
                    <i class="checkboxs " :class="{checkeds:address&&address.id===item.id}"></i>
                    <div>
                        <span style="margin-left:20px">{{item.receiveName}}</span>
                        <span style="margin-left:15px">{{item.tel}}</span>
                        <span class="mainlian" style="margin-left:15px" v-if="item.main"></span>
                    </div>
                    <div class="locations" style="margin-left:35px" >{{item.province}}{{item.city}}{{item.county}}{{item.locationAddr}}({{item.xiaoquName}}){{item.detailAddress}}</div>
                </div>
                <div class="tc mt2"><a class="btn-plain" @click="toAddAddress"></a><br/></div>
            </div>

                <div v-show="lian=='xing'">
                    <div style="padding:0 15px"  v-show="zhen=='from'">
                        <div class="input-wrap lite-divider">
                            <span class="fl fs15"></span>
                            <input placeholder="" class="fr fs14 hidden-input" v-model="submitAddress.receiveName"/>
                        </div>
                         <div class="input-wrap lite-divider">
                            <span class="fl fs15"></span>
                            <input type="tel" placeholder="" class="fr fs14 hidden-input" v-model="submitAddress.tel"/>
                        </div>
                        <div @click="showRegion" class="input-wrap lite-divider menu-link">
                            <span class="fl fs15" style="color: #3b3937"></span>
                            <span class="fr fs14"  style="color: #aeaeae" v-if="!distinct"></span>
                            <span class="fr fs14" v-if="distinct">{{distinct}}</span>
                        </div>
                        <!--    -->
                        <div v-if="selectRegion==true">
                            <div class="tc">
                                <div class="region  fl" :class="{check:currentRegionType==1}" @click="backRegion(1)"></div>
                                <div class="region fl" :class="{check:currentRegionType==2}" @click="backRegion(2)"></div>
                                <div class="region fl" :class="{check:currentRegionType==3}"></div>
                            </div>
                            <div   style="width:100%;clear:both;background-color: #e0dede; overflow:hidden">
                                <div v-for="(region,i) in regions" :class="{city:Math.floor((i/4)%2)==0,city2:Math.floor((i/4)%2)==1}" class="fs14 fl"  @click="updateRegion(region)">{{region.name}}</div>
                            </div>
                            <div> &nbsp;</div>
                        </div>

                         <div style="clear: both;" v-if="selectRegion==false">
                            <div class="input-wrap lite-divider  menu-link"   @click="showLocation"  >
                                <span class="fl fs15"></span>
                                <span class="fr fs14"  style="color: #aeaeae" v-if="submitAddress.xiaoquName==''"></span>
                                <span class="fr fs14" v-if="submitAddress.xiaoquName">{{submitAddress.xiaoquName}}</span>
                            </div>
                            <div class="input-wrap lite-divider">
                                <span class="fl fs15"></span>
                                <input placeholder="128" class="fr fs14 hidden-input" v-model="submitAddress.amapDetailAddr"/>
                            </div>
                            <div class="input-wrap lite-divider">
                                <span class="fl fs15"></span>
                                <input placeholder="1402" class="fr fs14 hidden-input" v-model="submitAddress.homeAddress"/>
                            </div>
                            <a href="#" class="btn" @click="addAddressa" ></a>
                            <a href="#" class="btn" @click="addfan" ></a>
                        </div> 
                    </div>
            </div> 
              	<!--	-->
                <div style="background-color: #fffff8" v-show="lian=='xiaoqu'">
                    <div class="location-wrap">
                        <div class="location-input-wrap">
                            <input placeholder="" class="location-input" v-model="suggestLocation" />
                            <i class="location-btn-cancel" @click="cancelLocation" v-if="suggestion"></i>
                        </div>
                        <span class="location-btn-ensure" @click="submitLocation"></span>
                    </div>
                    <div class="location-empty-tip" v-if="!suggestions.length">
                        
                    </div>
                    <div class="location-location" @click="chooseLocation(suggestion)" v-for="suggestion in suggestions" v-if="suggestions.length">
                        <span style="position:relative;font-color:#cccccc">{{suggestion._name}} - {{suggestion._address}}</span>
                    </div>
                </div>
        </div>

        <!--  -->
        <div v-show="page=='qian'">
            <div class="title-line">
                  <div class="title_text"></div>
                <div class="title_count">{{couponNum}}</div>
            </div>

             <div>
                <div class="coupon_item " v-for="(item,i) in coupons"  :key="item.id" :class="{selected:i==selectedIndex}" @click="chooseCoupon(i)">
                    <div class="outter_bg ">
                        <i class="icon_se " ></i>
                        <div class="coupon_desc">
                            <div class="coupon_line_1">
                                <span class="coupon-name">{{item.title}}</span>
                                <span class="coupon-time">{{item.leftDayDes}}</span>
                            </div>
                            <div class="coupon-limit">{{item.useStartDateStr}}{{item.useEndDateStr}}</div>
                        </div>
                        <div class="coupon_value">
                            <div class="coupon-amount">{{item.amount}}</div>
                            <div class="coupon-dyq"></div>
                        </div>
                    </div>
                </div>
            </div>
            <div style="height: 15px;width:100%">&nbsp;
            </div>
            <div class="btn_area" style="margin-bottom: 15px;">
                <div class="more_btn" @click="confirm"></div>
            </div>
        </div>
   </div>
</template>

<script>
let vm;
import wx from 'weixin-js-sdk';
import { MessageBox } from 'mint-ui';
import '../../node_modules/jquery/dist/jquery.min.js'
import '../../node_modules/jquery-datetimepicker/build/jquery.datetimepicker.full.js'
import '../../node_modules/jquery-datetimepicker/build/jquery.datetimepicker.min.css'
export default {
   data () {
       return {
           zzshow:true,
           page:'main',
           address:{},
           item:"",
           memo:"",
           serviceItemId:6,
           hours:2,
           amount:0,
           requireDate:'',
           memo:'',
           realAmount:0,
        //    
            addresses:[],
            checkedAddress:{},
            lian:'chu',
            zhen:'from',
            submitAddress:{
                receiveName:"",
                tel:"",
                provinceId:0,province:"",
                cityId:0,city:"",
                countyId:0,county:"",
                xiaoquName:"",
                amapId:0,
                amapDetailAddr:"",
                homeAddress:""
            },
            distinct:'',
            selectRegion:false,
            currentRegionType:1,
            regions:[],
            provinces:[],
            citys:[],
            countys:[],
            //
                suggestLocation:'',
                suggestion:{},
                suggestions:[],
                showg:false,
                showz:false,
            //
            coupons:[],
            allCoupons:[],
            couponNum:0,
            coupon:{},
            couponDesc:'',
            selectedIndex:-1,
            realMoney:0,
            billId:'',

       };
   },
    watch: {
        suggestLocation:{
                // console.log(nv,);
                 handler(nv, ov){
                        if(vm.suggestLocation.length>=2 && vm.suggestLocation!=vm.suggestion._name) {
                            vm.getSuggestion();
                        }
                 },
                    deep: true,
            },
   },
   created() {
       vm=this;
   },
   mounted() {
    //    let url1 = '/initSession4Test/105';
    //     vm.receiveData.getData(vm,url1,'data1',function(){
          
    //     })
       this.initInfo();
            $('#timetaker').click(function(){
                    $('#datetimepicker2').datetimepicker('show');
             });
             $.datetimepicker.setLocale('ch')
            $('#datetimepicker2').datetimepicker({
                onChangeDateTime:function(x){
                    var dt = vm.formatDate(x,'yyyy/MM/dd hh:mm:ss');
                    if(vm.requireDate!=dt){
                        vm.requireDate=dt;
                    }
                },
                allowTimes:['9:00','10:00','11:00','12:00','13:00','14:00','15:00','16:00','17:00','18:00'],
                format:'Y-m-d H:i',
                formatDate:'Y-m-d H:i',                           
            });
            
   },

   components: {},

   methods: {
        formatDate(date, fmt) {
            var currentDate = new Date(date);
            var o = {
                "M+": currentDate.getMonth() + 1, //
                "d+": currentDate.getDate(), //
                "h+": currentDate.getHours(), //
                "m+": currentDate.getMinutes(), //
                "s+": currentDate.getSeconds(), //
                "q+": Math.floor((currentDate.getMonth() + 3) / 3), //
                "S": currentDate.getMilliseconds() //
            };
            if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (currentDate.getFullYear() + "").substr(4 - RegExp.$1.length));
            for (var k in o)
                if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
            return fmt;
        },
        //
        showMemo() {
            MessageBox.prompt('','',).then(({ value, action }) => {
                    vm.memo=value;
                }).catch(err => { 
                    if (err == 'cancel') {     //
                        
                    }
                    });
        },
        initInfo() {
            let url="baojie/normal/payinfo/"+vm.serviceItemId;
            vm.receiveData.getData(vm,url,'res',function(){
                if(vm.res.success) {
                    vm.item=vm.res.result.item;
                    vm.address=vm.res.result.address;
                    vm.coupons=vm.res.result.coupons;
                    vm.allCoupons=vm.res.result.coupons;
                    vm.couponNum=vm.res.result.coupons.length;
                    vm.computeAmount();
                    vm.zzshow=false;
                }else {
                    alert(vm.res.message==null?"":vm.res.message);
                    vm.zzshow=false;
                }
             })
       },
       //
       addhours() {
           if(!vm.hours || vm.hours<2) {
               vm.hours=2
           }else {
               vm.hours+=0.5;
           }
          vm.computeAmount();
       },
        //
        subhours() {
            if(!vm.hours || vm.hours<=2) {
               vm.hours=2
           }else {
               vm.hours-=0.5;
           }
           vm.computeAmount();
        },
        //
        choseAddress() {
            vm.page='list';
            vm.dataAddress();
        },
        //
        dataAddress() {
            vm.receiveData.getData(vm,'/addresses','data',function() {
                if(vm.data.success) {
                    vm.addresses=vm.data.result;
                }else {
                    vm.addresses=[];
                }
            })
        },
        //
        checks(item){
             vm.address = item;
             vm.page="main"
        },
        toAddAddress() {
            vm.lian='xing'
        },
        showRegion() {
            vm.selectRegion=!vm.selectRegion;
            if( vm.selectRegion) {
                vm.changeRegionView();
            }
        },
         //
        updateRegion(region) {
            console.log(region)
            vm.changeRegionView(region.regionType,region.id,region.name)
        
        },
        //
        getRegions(type,id) {
            vm.currentRegionType=type
            vm.receiveData.getData(vm,'/regions/'+type+'/'+id,'data',function(){
                vm.regions=vm.data.result;
            },function() {
                alert("");
        }); 
       },
        //
    changeRegionView(regionType,regionId,regionNam) {
        if(!regionType){
            if(vm.provinces.length==0) {
                vm.getRegions(1,1);
            } else {
                vm.regions = provinces;
            }
    }else {
        if(regionType==1) {
            if(vm.submitAddress.provinceId != regionId ||vm.citys.length==0) {
		        vm.getRegions(2,regionId);
			} else {
				vm.regions = citys;
			}
			vm.submitAddress.province = regionNam;//
            vm.submitAddress.provinceId = regionId;//ID
            // console.log(vm.submitAddress.province, vm.submitAddress.provinceId )
			vm.currentRegionType=2;
        }else if(regionType == 2) {
			if(vm.submitAddress.cityId != regionId ||countys.length==0) {
		        vm.getRegions(3,regionId);
			} else {
				vm.regions = countys;
			}
			vm.submitAddress.city = regionNam;//
            vm.submitAddress.cityId = regionId;
            // console.log(vm.submitAddress.city,vm.submitAddress.cityId)
			vm.currentRegionType=3;
		} else if(regionType == 3) {
			vm.submitAddress.county = regionNam;
			vm.submitAddress.countyId = regionId;
			vm.distinct=vm.submitAddress.province+vm.submitAddress.city+vm.submitAddress.county;
            vm.selectRegion = false;
		 }
        }
    },
      backRegion(a) {
        vm.getRegions(a,1)
    },
    backRegion(b) {
        // console.log(vm.submitAddress.provinceId)
        vm.getRegions(b,vm.submitAddress.provinceId)
    },
    showLocation() {
        if(vm.submitAddress.city == ""||vm.submitAddress.county==""||vm.submitAddress.province=="") {
                alert('');
	    		return;
            }
        vm.suggestions=[];
        vm.lian='xiaoqu'

    },
      //
    cancelLocation() {
         vm.suggestLocation = '';
         vm.lian='xing';
    },
    submitLocation(){
        vm.submitAddress.xiaoquName = vm.suggestLocation;
        vm.submitAddress.amapId=vm.suggestion._id;
        vm.submitAddress.amapDetailAddr=vm.suggestion.detailaddress;
        vm.lian='xing';
    },
     //
    getSuggestion() {
         vm.receiveData.getData(vm, "amap/"+vm.submitAddress.city+"/"+vm.suggestLocation,'data',function(){
              vm.suggestions=vm.data.result;
        },function() {
            vm.suggestions=[];
        });
    },

     chooseLocation(suggestion) {
        vm.suggestion = suggestion;
        vm.suggestions=[];
        vm.suggestLocation = suggestion._name;
    },
    //
    addAddressa(){
        if(vm.submitAddress.province==""||vm.submitAddress.city==""||vm.submitAddress.county==""){
	    		alert("");
	    		return;
	    	}
	    	if(vm.submitAddress.amapDetailAddr==""||vm.submitAddress.receiveName==""
	    		||vm.submitAddress.tel==""||vm.submitAddress.homeAddress==""){
	    		alert("");
	    		return;
	    	}
	    	if(!(/^1[3-9][0-9]\d{4,8}$/.test(vm.submitAddress.tel))) {
	    		alert("");
	    		return;
            }
            	vm.saveAddress();
    },
     /**  */
     saveAddress() {
            var addr = {};
            addr.receiveName=vm.submitAddress.receiveName;
            addr.tel=vm.submitAddress.tel;
            addr.provinceId=vm.submitAddress.provinceId;
            addr.province=vm.submitAddress.province;
            addr.cityId=vm.submitAddress.cityId;
            addr.city=vm.submitAddress.city;
            addr.countyId=vm.submitAddress.countyId;
            addr.county=vm.submitAddress.county;
            addr.xiaoquName=vm.submitAddress.xiaoquName;
            addr.detailAddress=vm.submitAddress.amapDetailAddr+vm.submitAddress.homeAddress;
            addr.amapDetailAddr=vm.submitAddress.amapDetailAddr;
            addr.amapId=vm.submitAddress.amapId;
            // console.log(addr)
            vm.receiveData.postData(vm,'/addAddress',addr,'n',function(){
                // console.log(vm.n)
                    vm.addresses.push(vm.n);
                     vm.checkedAddress=vm.n;
                     
                    vm.page="main"
                    vm.lian='chu'
                })
     },
     //
     addfan() {
         vm.lian="chu";
     },
     //
     showCoupons() {
        vm.page='qian'
        vm.coupon=vm.coupon ? vm.coupon : {};
        vm.computeAmount();
     },
     showCouponList() {

     },
     //
     chooseCoupon(i) {
        if(this.selectedIndex != i) {
            this.selectedIndex=i
        }else {
            this.selectedIndex=-1;
        }
        vm.confirm();
     },
    //  
    confirm() {
        if( vm.selectedIndex<0 || vm.selectedIndex>= vm.coupons.length) {
               vm.chooseCoupons('');
           }else {
               vm.chooseCoupons(vm.coupons[vm.selectedIndex]);
            }
       vm.page='main';     
    },
    chooseCoupons(coupon) {
            if(coupon== '') {
                vm.coupon={};
                // vm.couponDesc=''
            }else {
                vm.coupon=coupon;
                // vm.couponDesc=""+coupon.amount+"";
            }
            vm.computeAmount()
        },
    //
    computeAmount() {
        vm.amount=vm.item.price*vm.hours;
        vm.filterCouponByAmount(vm.amount);
        vm.couponNum=vm.coupons.length;
        if(vm.coupon.amount&&!vm.canUse(vm.coupon,vm.amount)){
            vm.coupon = {};
        }
        vm.realMoney = vm.coupon.amount ? vm.amount - vm.coupon.amount : vm.amount;
      
        if(vm.realMoney<=0.01) {
        	vm.realAmount=0.01
        }else{
             vm.realAmount = vm.realMoney.toFixed(2);
        }

    },
    filterCouponByAmount(amount) {
         var c = [];
            for(var i=0;i<vm.allCoupons.length;i++){
                if(vm.canUse(vm.allCoupons[i],amount)){
                    c.push(vm.allCoupons[i]);
                }
            }
            vm.coupons= c;
    },
    canUse(coupon,amount) {
         return coupon.usageCondition<=amount;
    },
    //
    submit() {
        vm.createBill();
    },
    createBill() {
         if(vm.billId){
            vm.requestPay();
	         }
            var bill = {};
            bill.serviceItemId=vm.serviceItemId;
            bill.couponId = vm.coupon.id;
            bill.reqTime = vm.requireDate;
            bill.memo = vm.memo; 
            bill.addressId = vm.address.id;
            bill.count = vm.hours;
            bill.itemType = 302;
            if(!bill.addressId||bill.addressId<=0) {
                alert("");
                return;
            }
            if(!bill.reqTime){
                alert("");
                return;
            }
            let url="/baojie/normal";
             vm.receiveData.postData( vm, url,bill,'res', function(){
                 if(vm.res.success) {
                     vm.billId=vm.res.result;
                     vm.requestPay()
                 }else {
                     alert(vm.res.message==null?"":vm.res.message);
                 }
             })

    },
    requestPay() {
        vm.zzshow=true;
        let url="/baojie/pay/"+vm.billId;
         vm.receiveData.postData( vm, url,{},'res', function(){
           if(vm.res.success) {
             wx.config({
    			appId: vm.res.result.appId, // 
    			timestamp: vm.res.result.timestamp , // 
    			nonceStr: vm.res.result.nonceStr, // 
    			signature: vm.res.result.signature,// 1
    			jsApiList: ['chooseWXPay'] // JSJS2
            });
             wx.chooseWXPay({
            	"timestamp":vm.res.result.timestamp,
                "nonceStr":vm.res.result.nonceStr,
                "package":vm.res.result.pkgStr,
                "signType":vm.res.result.signType,
                "paySign":vm.res.result.signature,
               success: function (res) {
                   //common.invokeApi("POST", "baojie/notifyPayed/"+o.billId);
                    // 
                   alert("");
                //    location.href=success.html?oId="+o.billId
                vm.$router.push({path:'/success',query:{'oid':vm.billId}})
               },
               fail:function(res) {
         	    	alert(JSON.stringify(res));
                    vm.zzshow=false;
                },
                 cancel:function(res){
                    console.log(JSON.stringify(n));
                    vm.zzshow=false;
				}
               })
           }else {
               vm.zzshow=false;
               alert(vm.res.message==null?"":vm.res.message);
           }   
         })
    }

   },

   computed: {},
}
</script>

<style  scoped>
.zzmb {
    z-index: 100000;
    position: absolute;
    top: 0;
    left: 0;
    -moz-opacity: 0.65;
    opacity: 0.65;
    filter: alpha(opacity=65);
    background: #000;
    width: 100%;
    height: 100%;
    display: block;
}
.address{
    margin-top: 15px;
}
.addr_area{
    background: #F7F7F1;
}
.addrtop {
    background:url('../assets/img/icon_address_top_02.png') repeat-x;height:5px;background-size: 100% 5px;
}
.addrbottom {
    background:url('../assets/img/icon_address_bottom_04.png') repeat-x;height:5px;background-size: 100% 5px
}
.add_address{
    display: inline-block;
    font-size: 14px;
    height: 30px;
    line-height: 30px;
    border-radius: 3px;
    border: 1px solid #cfa972;
    padding:4px 30px;
    margin:20px 0;
    color:#cfa972;
    background-color:white;
    text-decoration: none;
}
.custom-menu-link {
    font-size:16px;
